.navbarpage a
{
    box-sizing:border-box;
    text-decoration:none;
    list-style:none ;
}
nav 
{
    background:black;
    height:100px;
}
label.logo
{
color:white;
}
nav ul
{
    float:right;
    margin-right:20px;
}
nav ul li
{
display:inline-block;
line-height:80px;
margin:0 5px;
}
nav ul li a
{
    color:white;
    font-size:20px;
    text-transform:uppercase;   
    margin:30px;
    padding:7px 13px;
    border-radius:5px;
}
a:hover
{
    background:#1b9bff;
    transition:0.5s;
}
.checkbtn
{
    font-size:30px;
    color:white;
    float:right;
    line-height:80px;
    margin-right:40px;
    cursor:pointer;
    display:none;
}
#check
{
    display:none;
}
@media (max-width:952px){
    label.logo{
        font-size:30px;
        padding-left:50px;
    }
    nav ul li a{
        font-size:16px;
    }
}
@media (max-width:858px)
{
    .checkbtn{display:block;}
    ul
    {
        position:fixed;
        width:100%;
        height:100vh;
        background:#2c3e50;
        top:80px;
        left:-100%;
        text-align:center;
        transition: all .5s;
    }
    nav ul li{
        display:block;
        margin: 50px 0;
        line-height:30px;
    }
    nav ul li a{
        font-size:20px;
    }
    a:hover{
        background:none;
        color:#0082e6;
    }
    #check:checked ~ ul
    {left:0;}
}
.navbarpage
{
    width:100vw;
    position:fixed !important;
    z-index:100;
}
